import { mapActions } from 'vuex'
export default {
  name: 'Products',
  props: ['data'],
  data() {
    return {
      cartItem: {
        id: this.data.id,
        nombre: this.data.name,
        url: this.data.foto[0].name,
        precio: this.data.price,
        quantity: 1,
      },
    }
  },
  methods: {
    ...mapActions(['addItemToCart']),
    displayMessage() {
      this.$swal({
        title: 'Cart Updated!',
        text: `${this.data.name} was added to your cart!`,
        icon: 'success',
        button: 'Ok',
      })
    },
  },
  filters: {
    formatPrice(price) {
      return `$${price}`
    },
  },
}
